@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;

    margin: 1rem;
}

.features-container__feature-title {
    flex: 1 1;
    max-width: 180px;
    margin-right: 2rem;
}

.features-container__feature-title h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -0.04em;
    color: #fff;
}

.features-container__feature-title div {
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}

.features-container_feature-text {
    flex: 2 1;
    max-width: 390px;
    display: flex;
}

.features-container_feature-text p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
}

@media screen and (max-width: 550px) {
    .features-container__feature-title h1 {
        font-size: 14px;
        line-height: 22px;
    }

    .features-container_feature-text p {
        font-size: 12px;
        line-height: 20px;
    }

    .features-container__feature {
        margin: 1rem 0;
    }
}
.features {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.features-heading {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    text-align: left;
    margin-right: 5rem;
}

.features-heading h1 {
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    font-family: var(--font-family);
}

.features-heading p {
    color: var(--color-subtext);
    font-size: 16px;
    line-height: 30px;
    font-weight: 500;
    font-family: var(--font-family);

    margin-top: 2rem;
}

.features-container {
    flex: 1.5 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}
p {
    font-size: 1.5em;
  }

@media screen and (max-width: 990px) {
    .features {
        flex-direction: column;
    }

    .features-heading {
        margin: 0 0 2rem 0;
    }
}


@media screen and (max-width: 550px) {
    .features-heading h1 {
        font-size: 28px;
        line-height: 38px;
    }
}
.footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    background: var(--color-footer);
}

.footer_heading {
    width: 100%;
    text-align: center;

    margin-bottom: 3rem;
}

.footer_heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 64px;
    line-height: 75px;
}

.footer-btn {
    display: flex;
    justify-content: center;
    align-items: center;

    padding: 1rem;
    border: 1px solid #fff;
    text-align: center;

    margin-bottom: 10rem;
    cursor: pointer;
}

.footer-btn p {
    font-size: 18px;
    font-family: var(--font-family);
    line-height: 21px;
    color: #fff;
    word-spacing: 2px;
}

.brand {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.brand div {
    flex: 1 1;
    max-width: 100px;
    min-width: 50px;
    margin: 0.5rem;

    display: flex;
    justify-content: center;
    align-items: center;
}


.copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.copyright p {
    font-size: 12px;
    font-family: var(--font-family);
    line-height: 15px;
    color: #fff;
}

@media screen and (max-width: 850px) {
    .footer_heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px) {
    .footer_heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

    .footer-btn p {
        font-size: 14px;
        line-height: 20px;
    }
}

@media screen and (max-width: 400px) {
    .footer_heading h1 {
        font-size: 27px;
        line-height: 38px;
    }
}
.header {
    display: flex;
    flex-direction: row;
}

.header-content {
    flex: 4 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    margin-right: 0.5rem;
}

.header-content h1 {
    font-family: var(--font-family);
    font-weight: 900;
    font-size: 70px;
    line-height: 80px;
    letter-spacing: -0.03em;
}

.header-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 30px;
    line-height: 35px;
    color: var(--color-text);
    -webkit-animation-name: shadow-drop-center;
            animation-name: shadow-drop-center;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    -webkit-animation-delay: 3s;
            animation-delay: 3s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-direction: normal;
            animation-direction: normal;
    -webkit-animation-fill-mode: none;
            animation-fill-mode: none;

}

.header-content__people {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;

    margin-top: 2rem;
}

.header-content__people img {
    width: 181.79px;
    height: 38px;
}

.header-content__people p {
    margin: 0 0 0 1rem;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 38px;
    color: #fff;
    text-align: center;
}

.header-image {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.header-image img {
    width:350px;
    
}


@-webkit-keyframes shadow-drop-center {
    0%{
      box-shadow: 0 0 0 0 transparent;
    }
    100%{
      box-shadow: 5px 5px 5px 5px rgba(255, 0, 0, 0.35);
    }
}


@keyframes shadow-drop-center {
    0%{
      box-shadow: 0 0 0 0 transparent;
    }
    100%{
      box-shadow: 5px 5px 5px 5px rgba(255, 0, 0, 0.35);
    }
}

@media screen and (max-width: 1050px) {
    .header {
        flex-direction: column;
    }

    .header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .header-content h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .header-content p {
        font-size: 16px;
        line-height: 24px;
    }
    
    .header-content__people {
        flex-direction: column;
    }

    .header-content__people p {
        margin: 0;
    }


}

@media screen and (max-width: 490px) {
    .header-content h1 {
        font-size: 36px;
        line-height: 48px;
    }

    .header-content p {
        font-size: 14px;
        line-height: 24px;
    }


}
.new {
    display: flex;
    flex-direction: row;
}

.new-image {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    margin-right: 2rem;
}

.new-image img {
    width: 100%;
    height: 100%;
}

.new-content {
    flex: 1 1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    flex-direction: column;
}

.new-content h4 {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #71E5FF;
}

.new-content h4:last-child {
    color: #ff8A71;
}

.new-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    margin: 1rem 0;
}

.new-content p {
    color: var(--color-text);
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;

    margin-bottom: 2rem;
}

@media screen and (max-width: 950px) {
    .new {
        flex-direction: column;
    }

    .new-image {
        margin: 1rem 0;
    }

    .new-image img {
        width: unset;
        height: unset;
    }

    .new-content {
        margin-top: 2rem;
    }
}

@media screen and (max-width: 700px) {
    .new-image img {
        width: 100%;
        height: 100%;
    }
}
.about {
